.contain {
  width: 100%;
  margin: auto;
  background-color: transparent;
  display: flex;
}

.card-container {
  margin-bottom: 5rem;
  width: 300px;
  height: 250px;
  background-color: var(--color-bg);
  border-radius: 15px;
  border: 2px solid var(--color-red);
  perspective: 1000px;
}
.card-container .title {
  padding: 1rem 0 2rem 2rem;
  color: var(--color-text);
  font-size: 1.2rem;
  font-weight: bolder;
}
.card-container .picture {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.card-container .picture img {
  position: absolute;
  width: 20rem;
  height: 14rem;
  top: 0;
  left: 0px;
  transform-style: preserve-3d;
  transition: all 0.75s ease-out;
  z-index: 5;
  filter: drop-shadow(10px 10px 30px rgba(0, 0, 0, 0.3));
  cursor: pointer;
  border-radius: 15px;
}