@charset "UTF-8";
#contact-me {
  width: 100%;
  margin-top: 5rem;
}
#contact-me article {
  width: 100%;
  margin: auto;
}
#contact-me .burgerC {
  z-index: 5;
}
#contact-me .burgerC:after {
  content: "☲";
  font-size: 1rem;
  cursor: pointer;
  color: var(--color-red);
}
@media screen and (min-width: 1750px) {
  #contact-me .burgerC {
    display: none;
  }
}
#contact-me .confirm {
  display: flex;
  justify-content: center;
  align-items: center;
}
#contact-me .title-section {
  width: 80%;
  margin: auto;
  display: flex;
}
#contact-me .title-section h1 {
  font-size: 1.8rem;
  padding-bottom: 3rem;
}
#contact-me .title-section ul {
  list-style-type: decimal-leading-zero;
  list-style-position: inside;
}
#contact-me .title-section ul li::marker {
  font-size: 1.2rem;
  color: var(--color-red);
}
#contact-me .title-section .separator {
  border-bottom: 2px solid lightgrey;
}
#contact-me .container-computer {
  width: 100%;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
#contact-me .computer {
  min-width: 500px;
  min-height: 320px;
}
#contact-me .computer .border-ecran {
  width: 70%;
  margin: auto;
  height: 250px;
  background-color: black;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 10px 0 20px rgba(0, 0, 0, 0.4);
}
#contact-me .computer .border-ecran .ecran {
  width: 90%;
  height: 85%;
  margin: auto;
  border-radius: 2px;
  background-color: var(--color-bg);
  overflow: auto;
}
#contact-me .computer .border-ecran .ecran button {
  font-size: 0.6rem;
  border-radius: 5px;
  width: 40px;
  height: 20px;
}
#contact-me .computer .border-ecran .ecran .ecran-nav {
  padding-top: 1rem;
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: auto;
}
#contact-me .computer .border-ecran .ecran .ecran-nav svg {
  width: 1rem;
}
#contact-me .computer .border-ecran .ecran .ecran-nav img {
  width: 1.5rem;
}
#contact-me .computer .border-ecran .ecran .ecran-nav ul {
  display: flex;
  flex-direction: row;
  list-style-type: decimal-leading-zero;
  color: var(--color-red);
}
#contact-me .computer .border-ecran .ecran .ecran-nav li {
  font-size: 0.6rem;
  margin-left: 1.5rem;
}
#contact-me .computer .border-ecran .ecran .ecran-nav li a {
  text-decoration: none;
  color: var(--color-text);
  font-weight: bolder;
}
#contact-me .computer .border-ecran .ecran .ecran-nav li a:hover {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: var(--color-red);
}
#contact-me .computer .border-ecran .ecran .menu-mobile-computer {
  height: 10px;
  background-color: transparent;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  visibility: hidden;
  opacity: 0;
  backdrop-filter: blur(6px);
  transition: width 1s ease, visibility 1s ease, opacity 0.8s ease, background-color 0.5s ease;
  z-index: 2;
}
#contact-me .computer .border-ecran .ecran .menu-mobile-computer ul {
  list-style-type: decimal-leading-zero;
  color: var(--color-red);
  width: 80%;
}
#contact-me .computer .border-ecran .ecran .menu-mobile-computer li {
  width: 100%;
  margin-left: 20px;
  display: inline;
}
#contact-me .computer .border-ecran .ecran .menu-mobile-computer li a {
  line-height: 0.4rem;
  text-decoration: none;
  color: var(--color-text);
  font-weight: bolder;
  font-size: 0.6rem;
}
#contact-me .computer .border-ecran .ecran .menu-mobile-computer li a:hover {
  color: var(--color-red);
}
@media screen and (min-width: 1750px) {
  #contact-me .computer .border-ecran .ecran .menu-mobile-computer {
    display: none;
  }
}
#contact-me .computer .border-ecran .ecran .contain {
  width: 100%;
  display: inline-block;
  overflow: auto;
}
#contact-me .computer .border-ecran .ecran .contain button {
  font-size: 0.6rem;
  border-radius: 5px;
  width: 60px;
  height: 30px;
  margin: auto;
}
#contact-me .computer .border-ecran .ecran .ecran-title {
  padding-top: 1rem;
  width: 65%;
  margin: auto;
  display: flex;
}
#contact-me .computer .border-ecran .ecran .ecran-title h2 {
  font-size: 1rem;
  width: 100%;
}
#contact-me .computer .border-ecran .ecran .ecran-title ul {
  list-style-type: decimal-leading-zero;
  list-style-position: outside;
}
#contact-me .computer .border-ecran .ecran .ecran-title ul li::marker {
  font-size: 0.8rem;
  color: var(--color-red);
}
#contact-me .computer .border-ecran .ecran .ecran-form {
  max-width: 80%;
  min-height: 100px;
  margin: auto;
  padding-top: 1rem;
  font-size: 0.5rem;
  overflow-wrap: break-word;
  color: var(--color-text);
  padding-bottom: 1rem;
}
#contact-me .computer .border-ecran .ecran .ecran-form p {
  padding-bottom: 0.4rem;
}
#contact-me .computer .dessous-ecran {
  width: 70%;
  height: 40px;
  margin: auto;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  box-shadow: 10px 0 20px rgba(0, 0, 0, 0.4);
  background: linear-gradient(to right, #bbbbbb, white);
}
#contact-me .contact-form {
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
  padding-bottom: 5rem;
}
#contact-me .contact-form label {
  padding-top: 1rem;
  padding-bottom: 0.4rem;
}
#contact-me .contact-form input {
  height: 50px;
  font-size: 1.2rem;
  background-color: var(--color-bg);
  color: var(--color-text);
  border: 2px solid var(--color-red);
  padding: 1rem;
  outline: none;
  border-radius: 5px;
}
#contact-me .contact-form input:focus {
  background-color: var(--color-opacityred);
}
#contact-me .contact-form textarea {
  font-size: 1.2rem;
  background-color: var(--color-bg);
  color: var(--color-text);
  border: 2px solid var(--color-red);
  padding: 1rem;
  outline: none;
  border-radius: 5px;
}
#contact-me .contact-form textarea:focus {
  background-color: var(--color-opacityred);
}

