    width: 70%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
  }
  #contact-me article {
    width: 100%;
    margin: auto;
  }
  #contact-me .confirm {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  #contact-me .title-section {
    height: 100vh;
    display: flex;
    align-items: center;
  }
  #contact-me .title-section h1 {
    font-size: 2.5rem;
  }
  #contact-me .contact-form {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  #contact-me .computer {
    height: 420px;
    min-width: 500px;
    max-width: 800px;
    width: 100%;
  }
