    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 80%;
  }
  #contact-me .computer .border-ecran .ecran .contain button {
    font-size: 0.6rem;
    border-radius: 5px;
    width: 60px;
    height: 30px;
    margin: auto;
    margin-top: 3rem;
  }
  #contact-me .computer .border-ecran .ecran .ecran-title {
    width: 100%;
    margin: auto;
  }
  #contact-me .computer .border-ecran .ecran .ecran-title h2 {
    font-size: 1.2rem;
    width: 100%;
  }
  #contact-me .computer .border-ecran .ecran .ecran-title ul {
    list-style-type: decimal-leading-zero;
    list-style-position: outside;
  }
  #contact-me .computer .border-ecran .ecran .ecran-title ul li::marker {
    font-size: 0.8rem;
    color: var(--color-red);
  }
  #contact-me .computer .border-ecran .ecran .ecran-form {
    width: 100%;
    max-width: 60%;
    height: 70%;
    font-size: 0.8rem;
  }
  #contact-me .computer .border-ecran .ecran .ecran-form p {
    padding-bottom: 0.4rem;
  }
  #contact-me .computer .dessous-ecran {
    width: 80%;
    height: 40px;
    margin: auto;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    box-shadow: 10px 0 20px rgba(0, 0, 0, 0.4);
    background: linear-gradient(to right, #bbbbbb, white);
  }
  #contact-me .contact-form {
    width: 60%;
    margin: auto;
    display: flex;
    flex-direction: column;
    padding-bottom: 5rem;
  }
  #contact-me .contact-form label {
    padding-top: 1rem;
    padding-bottom: 0.4rem;
  }
  #contact-me .contact-form input {
    height: 50px;
    font-size: 1.2rem;
    background-color: var(--color-bg);
    color: var(--color-text);
    border: 2px solid var(--color-red);
    padding: 1rem;
    outline: none;
    border-radius: 5px;
  }
  #contact-me .contact-form input:focus {
