  #contact-me .contact-form textarea {
    font-size: 1.2rem;
    background-color: var(--color-bg);
    color: var(--color-text);
    border: 2px solid var(--color-red);
    padding: 1rem;
    outline: none;
    border-radius: 5px;
  }
  #contact-me .contact-form textarea:focus {
