  }
}
@media screen and (min-width: 1750px) {
  .ecran-nav {
    display: none;
  }
  .ecran-nav svg {
    width: 1rem;
  }
  .ecran-nav img {
    width: 1.7rem;
  }
  .ecran-nav ul {
    display: none;
    flex-direction: row;
    list-style-type: decimal-leading-zero;
    color: var(--color-red);
  }
  .ecran-nav li {
    font-size: 0.6rem;
    margin-left: 2.5rem;
  }
}
@media screen and (max-width: 1750px) {
  .menu-desktop-computer {
    display: none;
  }
  .menu-desktop-computer ul {
    list-style-type: decimal-leading-zero;
    color: var(--color-red);
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .menu-desktop-computer li {
    margin-left: 5vw;
  }
  .menu-desktop-computer li a {
    line-height: 5rem;
    text-decoration: none;
    color: var(--color-text);
    font-weight: bolder;
  }
  .menu-desktop-computer li a:hover {
    color: var(--color-red);
  }
}