#my-work {
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 0;
}
#my-work .title-section {
  width: 100%;
  margin: auto;
  display: flex;
}
#my-work .title-section h1 {
  font-size: 1.8rem;
  padding-bottom: 3rem;
}
#my-work .title-section ul {
  list-style-type: decimal-leading-zero;
  list-style-position: inside;
}
#my-work .title-section ul li::marker {
  font-size: 1.2rem;
  color: var(--color-red);
}
#my-work p {
  padding-top: 1rem;
  width: 100%;
  font-size: 1.1rem;
  text-align: left;
  line-height: 1.7rem;
}

@media screen and (min-width: 1300px) {
  #my-work {
    width: 70%;
    margin: auto;
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    align-items: center;
  }
  #my-work .title-section {
    width: 100%;
    margin: auto;
    height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  #my-work .title-section h1 {
    font-size: 2.5rem;
    padding-bottom: 3rem;
  }
  #my-work article {
    width: 80%;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}